@use 'constants.scss'as *;

.header {
	background: #ffffff;

	.bigHeaderRow {
		@include flexRow();
		padding: 30px 50px 50px 50px;

		.menu {

			& a:first-of-type {
				margin-right: 42px;
			}

			a {
				color: $bgColor;
				font-size: 20px;


				&:hover {
					color: $activeLinkColor;
				}
			}
		}

		.logo:hover * {
			fill: $activeLinkColor;
		}
	}

	.title {
		padding-bottom: 60px;
		position: relative;

		.titleText {
			font-size: 90px;
			font-weight: 900;
			letter-spacing: 0.03em;
			text-align: center;
			color: $bgColor;
			text-shadow: 0px 5px 4px #00000040;
			position: relative;
			z-index: 10;

		}

		.bgTitle {
			position: absolute;
			left: calc(50% - 135px);
			top: -88px;
			width: 270px;
			z-index: 0;

			& * {
				fill: #24282f3b;
			}
		}
	}
}

@media screen and (max-width: 500px) {
	.header {

		.bigHeaderRow {
			padding: 30px;
			padding-bottom: 35px;

			.menu {
				& a:first-of-type {
					margin-right: 40px;
				}
			}
		}

		.title {
			.titleText {
				font-size: 45px;
				word-spacing: 0.08em;
			}

			.bgTitle {
				left: calc(50% - 100px);
				// top: 0px;
				top: calc(100% - 200px);
				width: 200px;
			}
		}
	}
}